* {
  font-family: 'Playfair Display', serif;
  font-family: 'Lato', sans-serif;
  box-sizing: border-box;
  font-size: 62.5%;
  font-weight: 400;
  letter-spacing: 2px;
  overflow: none;
  color: $white;

}

body {
  overflow-x: hidden;
}

::-webkit-scrollbar {
  display: none;
}


h1, h2, h3, h4, h5, h6 {
  user-select: none;
}

h1 {
  font-family: 'Playfair Display', serif;
  font-weight: 700;
  font-size: 11rem;
  color: $white;
  margin-bottom: 20px;
}

h2 {

  font-size: 2.5rem;
  font-family: 'Playfair Display', 'serif';
  margin-bottom: 70px;
}

h4 {
  font-size: 1.6rem;
}

h5 {
  font-size: 2rem;
}

p {
  font-size: 1.4rem;
  line-height: 2;
  // width: 50%;
  margin-left: 200px;


}

a {
  text-decoration: none;
  transition: $transition-time;

  // &:hover {
  //   opacity: 0.8;
  // }
}

ul {
  padding: 0;
  margin: 0;
}

li {
  text-decoration: none;
  list-style: none;
}

hr {
  width: 100px;
  border-color: $white;
  margin-bottom: 70px;
  margin-left: 0px;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}

em {
  font-size: 1.4rem;
  font-style: normal;
}

.page {
  width: 100vw;
  min-height: 100vh;
  position: absolute;
  top: 0;
  overflow: hidden;


  .page__content {
    width: 100vw;
    height: 100vh;
    background: $black;
    position: absolute;
    overflow-x: hidden;
    overflow-y: scroll;
    transform: skew(-28deg);
    padding: 0 20vw;

    > * {
      transform: skew(28deg);

    }

    .page__content__text {

      margin-bottom: 100px;

      p {
        width: 70%;
        margin-left: 0px;
      }
    }

  }


}

.button {

  cursor: pointer;
  transform: skew(0deg) !important;
  padding: 20px;
  width: 300px;
  margin: 0 auto;
  color: $white;
  border: none;
  margin: 0 auto;
  display: block;
  transition: opacity $transition-time;

  &:hover {
    opacity: 0.7;
  }

  h6 {
    transform: skew(28deg);
    text-align: center;
    font-size: 1.6rem;
    margin: 0;


  }



}

.scroll-button {

  color: $white;
  cursor: pointer;
  z-index: 11;

  p {
    transform: skew(0deg);
    margin-left: 40px;
    font-size: 1.6rem;
  }

  &__icon {
    height: 20px;
    width: 20px;
    // transform: scaleX(0.7) rotate(46deg);
    transform: skew(-28deg);
    position: absolute;
    top: 23px;
    border: 1px solid $white;
    transition: background-color $transition-time;
  }

  &.left {
    position: absolute;
    top: 50%;
    left: 0;
    transform: rotate(90deg) skew(-28deg) translateY(10%) translateX(-40%);
  }

  &.right {
    position: absolute;
    top: 50%;
    right: 0;
    transform: rotate(-90deg) skew(-28deg) translateY(10%) translateX(30%);
  }

  &.bottom {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: skew(28deg) translateX(-50%);
  }

  &.top {
    position: fixed;
    top: 20px;
    left: 50%;
    transform: skew(28deg) translateX(-50%);
  }
}

footer {

  width: 130vw;
  padding: 0 180px;
  margin-left: -40vw;

  a {
    font-size: 1.4rem;
    padding-bottom: 2px;
    // border-bottom: 1px solid white;

    &:hover {
          border-bottom: none;
    }
  }

  .footer__info {
    width: 100vw;
    padding-top: 30px;
    margin-bottom: 20px;

    p {
      margin-bottom: 0;
      margin-top: 0;
      margin-left: 50px;
    }
  }

  .footer__copyright {
    font-size: 1rem;
    opacity: 0.5;
    text-align: center;
    margin-left: 40vw;
    padding-bottom: 30px;
  }
}

.page__offer footer {
  margin-left: -20vw;
}


