.fade-enter {
  // opacity: 0;
  z-index: 1;
  left: 140%;
}

.fade-enter-active {
  // opacity: 1;
  left: 0;
  transition: 1s ease-in-out;
}

.fade-exit {
  // opacity: 1;
  left: 0;
}
.fade-exit-active {
  // opacity: 0;
  left: -140%;
  transition: 1s ease-in-out;
}

.trigger {
  position:absolute;

  &-1 {
    top: 10px;
  }

  &-2 {
    top: 50px;
  }

  &-3 {
    top: 140%;
  }

  &-4 {
    top: 230%;
  }
}


