@media screen and (max-width: $breakpoint-small) {

  h1 {
    font-size: 5rem;
    margin-top: 100px;
  }

  hr {
    margin-bottom: 20px;
  }



  .page {
    height: 100vh;
    overflow-x: visible;

    .page__content {
      width: 450px;
      height: 100vh;
      left: 130px;
      width: 500px;
      left: -60px;
      padding: 0 35vw;
      padding: 0 20vw;
      position: fixed;
    }
  }

  footer {

    width: 240%;
    margin-left: -50vw;
    padding: 0 390px 0 170px;

    p {
      margin-left: 20px;
      font-size: 1.2rem;
    }

    a {
      font-size: 1.2rem;

    }
  }
}

@media screen and (max-width: $breakpoint-extra-small) {

  .page {

    .page__content {
      width: 450px;
      // left: 130px;
    }
  }
}
